<template>
  <div class="quiz">
    <div v-if="hero">
      <dl>
        <div>
          <dt></dt>
          <dd>{{ this.hero.integrity }}</dd>
          <dt></dt>
          <dd>{{ this.hero.mercy }}</dd>
          <dt></dt>
          <dd>{{ this.hero.courage }}</dd>
          <dt></dt>
          <dd>{{ this.hero.body }}</dd>
          <dt></dt>
          <dd>{{ this.hero.strength }}</dd>
          <dt></dt>
          <dd>{{ this.hero.dexterity }}</dd>
          <dt></dt>
          <dd>{{ this.hero.wisdom }}</dd>
          <dt></dt>
          <dd>{{ this.hero.rudder ? "" : "" }}</dd>
        </div>
        <div v-if="tips">
          <dt></dt>
          <dd>{{ this.hero.job }}</dd>
          <dt></dt>
          <dd>{{ this.hero.place }}</dd>
        </div>
      </dl>
      <div class="control">
        score: {{ score }}
        <div>
          <input size="16" v-model="inputHero" autocomplete="on" list="kanas" />
          <datalist id="kanas">
            <option v-for="n in kanas" :key="n">{{n}}</option>
          </datalist>
          <button @click="check"></button>
          <button @click="viewTips"></button>
          <button @click="pass"></button>
        </div>
        <div>
          <div v-if="checked !== null">
            {{ this.hero.name }} {{ this.hero.kana }}
            <div v-if="checked == true" class="right"></div>
            <div v-if="checked == false" class="miss"></div>
            <button @click="questionReset"></button>
          </div>
        </div>
      </div>
      <div class="history">
        <span v-for="history in histories" :key="history">{{ history }}</span>
      </div>
    </div>
    <div v-else>
      <button @click="questionReset"></button>
    </div>
  </div>
</template>

<script>
import heros from "./../heros";

export default {
  name: "Quiz",
  data() {
    return {
      kanas: new Set(heros.heros.map(h => h.kana).sort()),
      histories: [],
      inputHero: "",
      tips: false,
      checked: null,
      hero: null,
      score: 20
    };
  },
  methods: {
    questionReset() {
      this.inputHero = "";
      this.tips = false;
      this.checked = null;
      this.hero = heros.heros[Math.floor(Math.random() * heros.heros.length)];
    },
    check() {
      // 
      if (!this.kanas.has(this.inputHero)) return;
      if (this.checked !== null) return;

      if (this.inputHero == this.hero.kana) {
        this.checked = true;
        this.score += this.tips ? 3 : 4
      } else {
        this.checked = false;
        this.score -= 2
      }
      this.addHistory();
    },
    viewTips() {
      this.tips = true;
    },
    pass() {
      this.score -= 2
      this.addHistory();
      this.questionReset();
    },
    addHistory() {
      this.histories.push(
        (this.checked ? (this.tips ? "" : "") : "") + this.hero.name
      );
    },
  }
};
</script>

<style scoped>
dt {
  float: left;
  clear: left;
  margin-right: 0.5em;
  width: 120px;
}

dd {
  float: left;
  margin-left: 1em;
}

input {
  /*  */
  font-size: 17px;
}

.right {
  color: red;
}
.miss {
  color: gray;
}
</style>
